import React, { PureComponent } from 'react'
import { Modal, Row, Col, Input, Form, Button, Tooltip, message } from 'antd'
import { CsxFormSelect } from 'Form'
import {
  API_PRODUCTIONFREQUENCYS_FEETYPE_LIST,
  API_GET_LOCATION_CODEANDNAME,
  API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_LIST
} from 'api'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const FormItem = Form.Item
import style from './style/Table.scss'
@connect(
  state => ({ ...state.operationalTypeCost, userLoation: state.userLoation }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {
    // 数据初始化
    const { modalData, getSingleDet } = this.props
    // 判断是新增还是编辑
    if (modalData) {
      //编辑
      const { id = '' } = modalData
      const params = {
        urlParams: [`${id}`]
      }
      getSingleDet(params, () => {})
    }
  }
  handleSelectChange = () => {}
  handleSubmit = e => {
    const { form, modalData, handleAddOrEdit, userLoation = {} } = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let reqObj = { ...values }
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            if (i == 'reservoirAreaCode') {
              reqObj.reservoirAreaName = reqObj[i].children
            }
            if (i == 'storageLocationCategoryCode') {
              reqObj.storageLocationCategoryName = reqObj[i].children
            }
            if (i == 'storageLocationTypeCode') {
              reqObj.storageLocationTypeName = reqObj[i].children
            }
            reqObj[i] = reqObj[i].value
          }
        }
        reqObj.warehouseCode = userLoation.value
        reqObj.warehouseName = userLoation.children
        let result = {
            data: reqObj
          },
          msg = '创建成功'
        // 判断是否是修改,加ID
        if (modalData && modalData.id) {
          result.data.id = modalData.id
          // result.urlParams = [modalData.id]
          msg = '编辑成功'
        }
        handleAddOrEdit({ ...result }, () => {
          message.success(msg)
          const { handleGetList, searchParams, page, size } = this.props
          handleGetList({ searchParams: { page, size, ...searchParams } })
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const { update } = this.props
    update({ isShowModal: false })
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { update } = this.props
    update({ modalData: { ...values } })
  }
  render() {
    const {
      modalTitle,
      form: { getFieldDecorator },
      modalTuttonLoading,
      modalData
    } = this.props
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    }
    const unitOptions = [
      { key: '1', value: 'H' }
      // {key: '2', value: 'Kg'},
    ]
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
        width='500px'
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <Row gutter={16}>
            <Col span={20}>
              <FormItem {...formItemLayout} label='地点编码' hasFeedback>
                {getFieldDecorator('location', {
                  initialValue: modalData.location,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    allowClear={true}
                    disabled={true}
                    dateApi={API_GET_LOCATION_CODEANDNAME}
                    selectItem={{
                      key: 'codeAndName',
                      valueKey: ['code', 'name']
                    }}
                    handleSelectChange={this.handleSelectChange.bind(
                      this,
                      'location'
                    )}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={20}>
              <FormItem {...formItemLayout} label='产线' hasFeedback>
                {getFieldDecorator('line', {
                  initialValue: modalData.line,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    allowClear={true}
                    disabled={true}
                    dateApi={API_PRODUCTIONFREQUENCYS_PRODUCTIONLINE_LIST}
                    selectItem={{
                      key: 'codeAndName',
                      valueKey: ['code', 'name']
                    }}
                    handleSelectChange={this.handleSelectChange.bind(
                      this,
                      'line'
                    )}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={20}>
              <FormItem {...formItemLayout} label='类型' hasFeedback>
                {getFieldDecorator('feeType', {
                  initialValue: modalData.feeType,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSelect
                   disabled={true}
                    dateApi={API_PRODUCTIONFREQUENCYS_FEETYPE_LIST}
                    selectItem={{ key: 'code', valueKey: ['name'] }}
                    handleSelectChange={this.handleSelectChange.bind(
                      this,
                      'feeType'
                    )}
                  />
                )}
              </FormItem>
            </Col>
            <Col span={20}>
              <FormItem {...formItemLayout} label='单位' hasFeedback>
                {getFieldDecorator('unit', {
                  initialValue: modalData.unit,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    disabled={true}
                    data={unitOptions}
                    selectItem={{ key: 'key', valueKey: 'value' }}
                    handleSelectChange={this.handleSelectChange.bind(
                      this,
                      'unit'
                    )}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={20}>
              <FormItem {...formItemLayout} label='固定价格(元)' hasFeedback>
                {getFieldDecorator('price', {
                  initialValue: modalData.price,
                  rules: [
                    {
                      required: true,
                      message: '请输入正数且不超过两位小数!',
                      pattern: /^(?!0+(?:\.0+)?$)(?:[1-9]\d*|0)(?:\.\d{1,2})?$/
                    }
                  ]
                })(<Input />)}
              </FormItem>
            </Col>
            <Col span={20}>
              <FormItem {...formItemLayout} label='成本要素' hasFeedback>
                {getFieldDecorator('cost', {
                  initialValue: modalData.cost
                  // rules: [{
                  //   required: true, message: '请填写!'
                  // }]
                })(<Input disabled={true} placeholder='可输入多个值,使用逗号“,”隔开' />)}
              </FormItem>
            </Col>
            <Col span={2}>
              <div>
                <Tooltip
                  placement='top'
                  title={'可输入多个值，多个值之间请使用逗号“,”隔开'}
                  trigger='hover'
                  overlayClassName={'tootip_in_table'}
                >
                  <img
                    className={style.img_in_table}
                    src='/image/questionTip.png'
                  />
                </Tooltip>
              </div>
            </Col>
          </Row>
          <Row type='flex' justify='center'>
            <Col>
              <Button
                style={{ marginRight: '48px' }}
                onClick={this.handleCloseModal}
              >
                取消
              </Button>
              <Button
                type='primary'
                htmlType='submit'
                loading={modalTuttonLoading}
              >
                保存
              </Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



