import React, { PureComponent } from 'react'
import { Table, Modal, message } from 'antd'
const confirm = Modal.confirm
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
@connect(
  (state) => ({ ...state.operationalTypeCost, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
    this.columns = [
      {
        title: '地点',
        width: 160,
        dataIndex: 'location',
        key: 'location',
        render: (text, record, index) => {
          const { locationCode, locationName } = record
          return `${locationCode} ${locationName}`
        }
      },
      {
        title: '产线',
        width: 150,
        dataIndex: 'line',
        key: 'line',
        render: (text, record, index) => {
          const { lineCode, lineName } = record
          return `${lineCode} ${lineName}`
        }
      },
      {
        title: '类型',
        width: 100,
        dataIndex: 'feeType',
        key: 'feeType',
        render: (text, record) => {
          const { feeTypeName } = record
          return feeTypeName
        }
      },
      {
        title: '单位',
        width: 100,
        dataIndex: 'unit',
        key: 'unit',
        render: (text, record, index) =>
          text == '1' ? 'H' : text == '2' ? 'KG' : ''
      },
      {
        title: '固定价格（元）',
        width: 120,
        dataIndex: 'price',
        key: 'price'
      },
      {
        title: '成本要素',
        width: 200,
        dataIndex: 'cost',
        key: 'cost'
      },
      {
        title: '最后更新时间',
        width: 150,
        dataIndex: 'updateTime',
        key: 'updateTime'
      },
      {
        title: '操作人员',
        width: 100,
        dataIndex: 'updateBy',
        key: 'updateBy'
      },
      {
        title: '操作',
        width: 120,
        dataIndex: 'x',
        key: 'x',
        // fixed: 'right',
        render: (text, record, index) => (
          <div className="tableOperation">
            {this.renderBtn(text, record, index)}
          </div>
        )
      }
    ]
  }
  renderBtn(text, record, index) {
    const { menuPermissionIds } = this.props
    let dom = (
      <span
        className={
          menuPermissionIds &&
          menuPermissionIds.includes('operationalTypeCost_editInTable')
            ? 'blue'
            : 'disabledBtnInTable'
        }
        onClick={() => {
          if (menuPermissionIds.includes('operationalTypeCost_editInTable')) {
            this.onEdit(text, record, index)
          }
        }}
      >
        编辑
      </span>
    )
    return dom
  }
  handleOperator(text, record, index, type) {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      handleOperator
    } = this.props
    confirm({
      title: '温馨提示',
      content: '是否删除该储位？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleOperator(
          {
            data: {
              id: record.id
            },
            urlParams: [record.id]
          },
          () => {
            message.success('操作成功')
            handleGetList({ searchParams: { page, size, ...searchParams } })
          }
        )
      }
    })
  }
  onEdit(text, record, index) {
    const { update } = this.props
    update({
      isShowModal: true,
      modalTitle: '作业类型费用维护',
      modalData: record
    })
  }
  componentDidMount() {
    const { handleGetList, searchParams, page, size } = this.props
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, userLoation } = this.props
    const newUserLoationValue =
      (newProps && newProps.userLoation && newProps.userLoation.value) || ''
    const userLoationValue = (userLoation && userLoation.value) || ''
    if (newUserLoationValue != userLoationValue) {
      handleGetList({
        searchParams: {
          page: 1,
          size: newProps.size,
          ...newProps.searchParams
        }
      })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      selectedRowKeys,
      searchResult = []
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['10', '50', '100', '200', '500']
    }
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys, selectedRows })
      }
    }
    return (
      <div className="table-group">
        <Table
          rowKey={(record) => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{ x: countTableScroll(this.columns) }}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



