import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import Form from './components/./Form'
import Table from './components/./Table'
import Tool from './components/./Tool'
import OperationModal from './components/./OperationModal'
import { connect } from 'react-redux'
import { mapDispatchToProps } from './redux/mapDispatchToProps'

@connect(
  state => ({
    ...state.operationalTypeCost,
    menuPermissionIds: state.menuPermissionIds
  }),
  mapDispatchToProps
)
export default class App extends PureComponent {
  constructor(props) {
    super(props)
  }
  componentWillUnmount() {
    this.props.update({ searchResult: [], searchParams: {} })
  }
  render() {
    const {
      menuPermissionIds
     } = this.props
    return (
      <section>
        <Bread route={['设置中心', '产线管理', '作业类型费用设置']} />
        <Form />
        {/* <Tool menuPermissionIds={menuPermissionIds}/> */}
        <Table menuPermissionIds={menuPermissionIds}/>
        {this.props.isShowModal ? <OperationModal /> : null}
      </section>
    )
  }
}



