import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_OPERATIONALTYPECOSTS_PAGE,
  API_OPERATIONALTYPECOSTS_DEL,
  API_OPERATIONALTYPECOSTS_CREATE,
  API_OPERATIONALTYPECOSTS_UPDATE,
  API_OPERATIONALTYPECOSTS_DETAIL
} from 'api'
//同步
export const update = object => ({
  type: actionType.OPERATIONALTYPECOSTS_UPDATE,
  data: object
})

// 获取 - 商品属性列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.OPERATIONALTYPECOSTS_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_OPERATIONALTYPECOSTS_PAGE, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page = '', size = '' } = reqData ? reqData.searchParams : {}
      const { result = {} } = data
      const { total = '', list = '' } = result
      dispatch({
        type: actionType.OPERATIONALTYPECOSTS_UPDATE,
        data: {
          page,
          size,
          total,
          tableLoading: false,
          searchResult: list
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.OPERATIONALTYPECOSTS_FAIL
      })
    })
}
// 删除 - 商品属性数据
export const handleOperator = (reqData, callBack) => dispatch => {
  getData({ api: API_OPERATIONALTYPECOSTS_DEL, ...reqData })
    .then(data => {
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.OPERATIONALTYPECOSTS_FAIL
      })
    })
}
// 获取 - 单个属性数据
export const getSingleDet = (reqData, callBack) => dispatch => {
  console.log('reqData is ', reqData)
  getData({ api: API_OPERATIONALTYPECOSTS_DETAIL, ...reqData })
    .then(data => {
      callBack && callBack(data)
      const { result } = data
      const {
        batch = '',
        locationCode = '',
        locationName = '',
        feeType = '',
        feeTypeName = '',
        lineCode = '',
        lineName = '',
        unit = ''
      } = result
      let unitVal = unit == '1' ? 'H' : unit == '2' ? 'Kg' : ''
      let dataFormater = {
        ...result,
        location: {
          value: `${locationCode} ${locationName}`,
          children: `${locationCode} ${locationName}`
        },
        line: {
          value: `${lineCode} ${lineName}`,
          children: `${lineCode} ${lineName}`
        },
        batch: { value: `${batch}`, children: `${batch}` },
        feeType: { value: `${feeType}`, children: `${feeTypeName}` },
        unit: { value: `${unit}`, children: `${unitVal}` }
      }
      console.log('dataFormater data is', dataFormater)
      dispatch({
        type: actionType.OPERATIONALTYPECOSTS_UPDATE,
        data: {
          modalData: dataFormater
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.OPERATIONALTYPECOSTS_FAIL
      })
    })
}
// 创建/编辑 - 商品属性数据
export const handleAddOrEdit = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.OPERATIONALTYPECOSTS_UPDATE,
    data: { modalTuttonLoading: true }
  })
  let api =
    reqData.data && reqData.data.id
      ? API_OPERATIONALTYPECOSTS_UPDATE
      : API_OPERATIONALTYPECOSTS_CREATE
  getData({ api, ...reqData })
    .then(data => {
      console.log('res data 8888 is', data)
      // 相应的逻辑判断
      dispatch({
        type: actionType.OPERATIONALTYPECOSTS_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack(data)
    })
    .catch(err => {
      dispatch({
        type: actionType.OPERATIONALTYPECOSTS_FAIL
      })
    })
}



